@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,600,700|Poppins:400,600,700");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  color: #111111;
  font-size: 16px;
  line-height: 1.6;
  font-family: 'Poppins', sans-serif;
}

a {
  text-decoration: none;
  color: #111111;
  transition: all 200ms ease-in-out;
}

a:hover {
  color: #FF851B;
}

.full-height {
  height: 100%;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-space--between {
  justify-content: space-between;
}

.flex-space--around {
  justify-content: space-around;
}

.flex-align--center {
  align-items: center;
}

.flex-align--end {
  align-items: flex-end;
}

.flex-direction--column {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.showcase {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #1e1e1e;
}

.showcase-panel {
  width: 80%;
  border-radius: 10px;
  height: 600px;
  overflow: hidden;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.codetempo {
  color: #fff;
  height: 100%;
}

.codetempo-header {
  padding: 30px 0 30px;
}

.codetempo-content {
  padding: 0 40px;
  padding-right: 120px;
  height: 100%;
  flex: 1;
  position: relative;
  z-index: 99;
  box-shadow: 0 19px 38px rgba(0, 0, 0, 0.4), 0 15px 12px rgba(0, 0, 0, 0.32);
}

.codetempo-content, .codetempo-thumb {
  width: 50%;
}

.codetempo-right {
  height: 100%;
}

.codetempo-thumb {
  height: 100%;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  position: relative;
}

.codetempo-thumb:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
}

.codetempo-title {
  font-size: 60px;
}

.codetempo-titles {
  position: relative;
}

.codetempo-titles:after {
  content: '';
  width: 80px;
  height: 5px;
  background: #fff;
  display: block;
  margin-top: 40px;
}

.codetempo-footer {
  padding: 0 0 30px;
}

.codetempo-footer--title {
  font-size: 15px;
  margin-bottom: 3px;
}

.codetempo-footer--copy {
  font-size: 13px;
}

.codetempo-meta--block {
  height: 100px;
  width: 200px;
  background: pink;
}
